.customer__text{

    margin-left: auto;
    margin-right: auto;
  position: relative;
  display: block;
  font-family: 'Montserrat';
  font-style: normal;
  max-width: 280px;
  height: 256px;
   @media screen and (min-width: 768px){
    max-width: 705px;
  height: 138px;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.86;
  letter-spacing: 0.04em;
   }
    @media screen and (min-width: 1200px){
      max-width: 968px;
      font-size: 16px;
    }
  background-color: #84A178;
  font-weight: 400;
  font-size: 12px;
  line-height: 2.58;
  text-align: center;
  color: #FCFCFC;
  padding: 34px 39px;
  border-radius: 10px;
  margin-top: 32px;
  
  
}
.customer__title{
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  @media screen and (min-width: 768px){
    font-size: 16px;
  }
  @media screen and (min-width: 1200px){
    margin-top: 32px;
  }
  line-height: 1.85;
  text-align: center;
  color: #131313;
  margin-top: 30px;
  }
.swiper {
  
  width: 100%;
  height: 100%;
}
.swiper-pagination-bullet{
  background-color: #84A178;
  width: 12px;
  height: 12px;
}
.swiper-pagination-bullet-active {
  background-color: #84A178;
}
.swiper-pagination{
  position: relative;
  margin-top: 14px;
}